function Titres() {
    return (
        <Container>
            <Row className='title-citce'>
                <h2 className="we-are">We are a global company <br/>with local culture</h2>

                <div className="divlinks">
                    {tab.map(el =>
                    // <Col lg={3} md={6} sm={12}>
                        <a href={el.cite} className='links'> {el.link} </a>
                    // </Col> 
                   )}
                </div>
            </Row>

        </Container>
    )
}

export default Titres;